/**
 * @module app.offline
 */
const exports = {};

import './offline.less';
import './common_dependencies.js';
import olMap from 'ol/Map.js';

import olView from 'ol/View.js';
import olLayerTile from 'ol/layer/Tile.js';
import olSourceOSM from 'ol/source/OSM.js';
import ngeoBase from 'ngeo/index.js';
import ngeoMapModule from 'ngeo/map/module.js';
import ngeoOfflineModule from 'ngeo/offline/module.js';

// Useful to work on example - remove me later
import 'bootstrap/js/modal.js';
import 'jquery-ui/ui/widgets/resizable.js';
import 'jquery-ui/ui/widgets/draggable.js';
ngeoBase.baseModuleTemplateUrl = '../../src/';

/** @type {!angular.Module} **/
exports.module = angular.module('app', [
  'gettext',
  ngeoBase.module.name,
  ngeoMapModule.name,
  ngeoOfflineModule.name
]);


/**
 * @param {ngeoFeatureOverlayMgr} ngeoFeatureOverlayMgr ngeo feature overlay manager service.
 * @constructor
 * @ngInject
 */
exports.MainController = function(ngeoFeatureOverlayMgr) {

  /**
   * Save a square of 10 km sideways (Map's unit is the meter).
   * @type {number}
   * @export
   */
  this.offlineExtentSize = 10000;

  /**
   * @type {ol.Map}
   * @export
   */
  this.map = new olMap({
    layers: [
      new olLayerTile({
        source: new olSourceOSM()
      })
    ],
    view: new olView({
      center: [0, 0],
      zoom: 4
    })
  });

  ngeoFeatureOverlayMgr.init(this.map);
};


exports.module.controller('MainController', exports.MainController);


export default exports;



